import React from "react";
import styled, { keyframes } from "styled-components";
import html from "../assets/icons/html.svg";
import css from "../assets/icons/css.svg";
import js from "../assets/icons/js.svg";
import mysql from "../assets/icons/mysql.svg";
import node from "../assets/icons/node.svg";
import php from "../assets/icons/php.svg";
import react from "../assets/icons/react.svg";
import sass from "../assets/icons/sass.svg";
import bootstrap from "../assets/icons/bootstrap.svg";
import csharp from "../assets/icons/csharp.svg";
import git from "../assets/icons/git.svg";
import linux from "../assets/icons/linux.svg";
function Abilities({ color }) {
  const Abilities = styled.div`
  text-align:center;
    background: ${color.blueDark};
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    font-family: Roboto;
    gap:1em;
    color: ${color.Text};
    border-radius: 23px 23px 53px 0px;
    h1 {
      margin: 2em 0;
    }
    p{
        width:60%;
    }

    ul{
      width:70%;
      display:grid;
      list-style: none;
      gap:2em;
    }
  `;
  const fadeIn = keyframes`
    0% {
      opacity: 0;
    }
    10% {
      opacity: 0;
    }
    50% {
      opacity: 0;
    
    }
    100% {
      opacity: 1;
    
    }
  `;
  const GridCard = styled.div`
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 1em;
    width: 80%;
    img {
      animation: 2s ${fadeIn} ease-out;
      width: 90%;
      
      border-radius: 10px;

      &:hover{
        width: 100%;
      
        background-color: #fff;
      }
    }
  `;
  return (
    <Abilities>
      <h1>Habilidades</h1>
      <p>Cuento con la experiencia en las siguientes areas:</p>
      <GridCard>
        <img src={html} alt="html" />
        <img src={css} alt="css" />
        <img src={js} alt="js" />
        <img src={react} alt="react" />
        <img src={sass} alt="sass" />
        <img src={bootstrap} alt="bootstrap" />
        <img src={mysql} alt="mysql" />
        <img src={node} alt="node" />
        <img src={php} alt="php" />

        <img src={csharp} alt="csharp" />
        <img src={git} alt="git" />
        <img src={linux} alt="linux" />
        
      </GridCard>
      <h3>Mi educacion</h3>
      <ul>
          <li><b>Universidad Autónoma Metropolitana Azcapotzalco:</b>Licenciatura en Ingeniería en Computación 2020 </li>
          <li><b>Centro de Bachillerato tecnologico  Industrial y de servicios No.59:</b> Tecnico en programacion 2017-2020 </li>
          



      </ul>
    </Abilities>
  );
}

export default Abilities;
